h3.form_title {
    margin-top: 30px;
    margin-bottom: 0;
}

.form_section__fields {
    display: flex;
    margin-left: -20px;
    align-items: flex-end;
    justify-content: flex-start;
    flex-wrap: wrap;
    -webkit-box-pack: start;
}

.form_control_wrapper {
    width: calc(33.3333%);
    margin: 0;
    display: block;
    margin-top: 25px;
    padding-left: 20px;
}

.form_control {
    width: 100%;
}

.form_item {
    width: 100%;
}

a.conditions {
    color: #00afff;
}

.form_checkbox__label {
    font-weight: lighter;
}


.form__btn_wrapper {
    position: relative;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    padding-top: 10px;
}


.section--form__button {
    padding: 0px 10px;
    width: 166px;
    display: inline-block;
}

.form__btn_description {
    position: relative;
    display: block;
    color: rgb(140, 140, 140);
    font-size: 12px;
    line-height: 18px;
    padding: 0px 0px 0px 20px
}

@media (max-width: 767px) {
    .form_section__fields {
        flex-direction: column;
    }

    .form_control_wrapper {
        width: 100%;
    }

    .form__btn_description {
        display: none;
    }

    .section--form__button {
        width: 100%;
    }
}